
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.page-home {
  text-align: center;
}
.btn-group {
  text-align: center;
}
.btn-group .btn {
  display: inline-block;
  padding: 10px 20px;
  margin: 5px;
  line-height: 20px;
  border-radius: 20px;
  cursor: pointer;
  /*border: 1px solid @green;*/
  background-color: #fff;
  color: #42b983;
}
.btn-group .btn.active {
  color: #fff;
  background-color: #42b983;
}



